import { Button } from "@material-ui/core"
import { useState } from "react";
import { useHistory } from "react-router";

const Create = () => {
    const [title, setTitle] = useState('');
    const [body, setBody] = useState('');
    const [author, setAuthor] = useState('mario');
    const [isPending, setPending] = useState(false);

    const history = useHistory();
    
    const handleSubmit = () =>{
        setPending(true);
        const blog = {title: title, body: body, author: author};
        setTimeout(() => {
            fetch("http://localhost:8000/blogs", {
                method: "POST",
                headers: {"Content-Type": "application/json"},
                body: JSON.stringify(blog)
            }).then(res => {
                setPending(false);
                history.push('/');
            }).catch(err => {
                console.log(err.message)
            })
        }, 2000)
    }
    return (
        <div className="create">
            <h1>Add A New Blog</h1>
            <form>
                <label>Blog Title:</label>
                <input 
                    type="text"
                    required
                    value={ title }
                    onChange={ e => setTitle(e.target.value) }
                />
                <label>Blog Details:</label>
                <textarea 
                    value = { body }
                    required
                    onChange={ e => setBody(e.target.value) }
                ></textarea>
                <label>Blog Author</label>
                <select
                    value={ author }
                    onChange={ e => setAuthor(e.target.value) }
                >
                    <option value="mario">mario</option>
                    <option value="yoshi">Yoshi</option>
                </select>
                { !isPending && <Button color="secondary" variant="contained" onClick={() => handleSubmit()}>Add Blog</Button> }
                { isPending && <Button color="secondary" variant="contained" disabled>Adding Blog...</Button> }
                
            </form>
            <p>{ title }</p>
            <p>{ body }</p>
            <p>{ author }</p>
        </div>
    );
}

export default Create;